ents in your app.\r\n\r\n// Include the common styles for Angular Material. We include this here so that you only\r\n// have to load a single css file for Angular Material in your app.\r\n// Be sure that you only ever include this mixin once!\r\n@include mat-core();\r\n\r\n// Define the palettes for your theme using the Material Design palettes available in palette.scss\r\n// (imported above). For each palette, you can optionally specify a default, lighter, and darker\r\n// hue. Available color palettes: https://material.io/design/color/\r\n$admin-angular-primary: mat-palette($mat-indigo);\r\n$admin-angular-accent: mat-palette($mat-pink, A200, A100, A400);\r\n\r\n// The warn palette is optional (defaults to red).\r\n$admin-angular-warn: mat-palette($mat-red);\r\n\r\n// Create the theme object. A theme consists of configurations for individual\r\n// theming systems such as \"color\" or \"typography\".\r\n$admin-angular-theme: mat-light-theme((\r\n  color: (\r\n    primary: $admin-angular-primary,\r\n    accent: $admin-angular-accent,\r\n    warn: $admin-angular-warn,\r\n  )\r\n));\r\n\r\n// Include theme styles for core and each component used in your app.\r\n// Alternatively, you can import and @include the theme mixins for each component\r\n// that you are using.\r\n@include angular-material-theme($admin-angular-theme);\r\n\r\n/*\r\nTemplate Name: Admin Press Admin\r\nAuthor: Themedesigner\r\nEmail: niravjoshi87@gmail.com\r\nFile: scss\r\n*/\r\n/**\r\n * Table Of Content\r\n *\r\n * \t1. Globals\r\n *\t2. Headers\r\n *\t3. Navigations\r\n *\t4. Banners\r\n *\t5. Footers\r\n *\t6. app\r\n *\t7. Widgets\r\n *\t8. Custom Templates\r\n */\r\n \r\n@import 'variable'; \r\n@import \"../../../node_modules/bootstrap/scss/bootstrap\";\r\n@import 'app';\r\n@import 'pages';\r\n@import 'sidebar';\r\n@import 'vertical-header';\r\n\r\n@import '../scss/icons/font-awesome/css/font-awesome.min.css';\r\n@import '../scss/icons/simple-line-icons/css/simple-line-icons.css';\r\n@import '../scss/icons/themify-icons/themify-icons.css'; \r\n@import \"../scss/icons/material-design-iconic-font/css/materialdesignicons.min.css\";\r\n@import \"rtl/rtl\";\r\n@import 'widgets';\r\n@import 'grid';\r\n@import 'responsive';  \r\n@import 'spinners.scss'; \r\n@import 'animate.css';       \r\n@import 'theme-settings.scss';   \r\n\r\n// color css\r\n\r\n@import 'colors/blue-dark';\r\n@import 'colors/default-dark';\r\n@import 'colors/red-dark';\r\n@import 'colors/green-dark';\r\n@import 'colors/purple-dark';\r\n@import 'colors/megna-dark';\r\n@import 'colors/megna';\r\n@import 'colors/blue';\r\n@import 'colors/red';\r\n@import 'colors/green';\r\n@import 'colors/purple';\r\n@import 'colors/default';\r\n\r\n// dark theme\r\n@import 'dark';\r\n//In This scss you can write your scss\r\n@import 'custom';","/*\r\nTemplate Name: Admin Press Admin\r\nAuthor: Themedesigner\r\nEmail: niravjoshi87@gmail.com\r\nFile: scss\r\n*/\r\n// Variables\r\n@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700');\r\n\r\n$bodyfont:'Poppins', sans-serif;\r\n$headingfont:'Poppins', sans-serif; \r\n\r\n\r\n/*Theme Colors*/\r\n\r\n$topbar: #1a1d4a;\r\n$sidebar: #fff;\r\n$sidebar-white: #e5edef;\r\n$sidebar-alt:#f2f6f8;\r\n$bodycolor: #eef5f9;\r\n$headingtext: #455a64;\r\n$bodytext: #7d8b92; \r\n$sidebar-text: #fff;\r\n$sidebar-icons: #fff ;\r\n\r\n$light-text: #a6b7bf;\r\n$themecolor: #1a1d4a;\r\n$themecolor1: #09e3a9;\r\n$themecolor-alt: #26c6da;\r\n$themecolor-dark: #028ee1;\r\n\r\n/*bootstrap Color*/\r\n$danger: #ef5350;\r\n$success: #26dad2;\r\n$warning: #ffb22b;\r\n$primary: #1a1d4a;\r\n$info: #1976d2;\r\n$inverse: #2f3d4a;\r\n$muted: #99abb4;\r\n$dark: #263238;\r\n$light: #f2f4f8;\r\n$extra-light: #ebf3f5;\r\n$bglight:rgba(0, 0, 0, 0.02);\r\n\r\n/*Light colors*/\r\n$light-danger: #f9e7eb;\r\n$light-success: #e8fdeb;\r\n$light-warning: #fff8ec;\r\n$light-primary: #f1effd;\r\n$light-info: #cfecfe;\r\n$light-inverse: #f6f6f6;\r\n$light-megna: #e0f2f4; \r\n\r\n\r\n