\n    }\r\n}\r\n\r\n// Left-sidebar line\r\n.nav-devider{\r\n    height: 1px;\r\n    background: rgba(120, 130, 140, 0.13);\r\n    display: block;\r\n    margin: 15px 0;\r\n}\r\n\r\n/*******************\r\nFile Upload \r\n******************/\r\n\r\n.fileupload {\r\n    overflow: hidden;\r\n    position: relative;\r\n    input.upload {\r\n        cursor: pointer;\r\n        filter: alpha(opacity=0);\r\n        font-size: 20px;\r\n        margin: 0;\r\n        opacity: 0;\r\n        padding: 0;\r\n        position: absolute;\r\n        right: 0;\r\n        top: 0;\r\n    }\r\n}\r\n\r\n/*******************\r\nMegamenu\r\n******************/\r\n\r\n.mega-dropdown {\r\n    position: static!important;\r\n    width: 100%;\r\n    \r\n    .dropdown-menu {\r\n        width: 100%;\r\n        padding: 30px;\r\n        margin-top: 0px;\r\n        transform:translate(0px, 58px)!important;\r\n    }\r\n    ul {\r\n        padding: 0px;\r\n\r\n        li {\r\n            list-style: none;\r\n        }\r\n    }\r\n    .carousel-item .container {\r\n        padding: 0px;\r\n    }\r\n    .nav-accordion {\r\n        .card {\r\n            margin-bottom: 1px;\r\n        }\r\n        .card-header {\r\n            background: $white;\r\n            h5 {\r\n                margin: 0px;\r\n                a {\r\n                    text-decoration: none;\r\n                    color: $bodytext;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n/*******************\r\nList-style-none\r\n******************/\r\n\r\nul.list-style-none {\r\n    margin: 0px;\r\n    padding: 0px;\r\n    li {\r\n        list-style: none;\r\n        a {\r\n            color: $bodytext;\r\n            padding: 8px 0px;\r\n            display: block;\r\n            text-decoration: none;\r\n            &:hover {\r\n                color: $themecolor;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n/*******************\r\ndropdown-item\r\n******************/\r\n\r\n.dropdown-item {\r\n    paddin