n.container-fluid {\r\n  padding: 0 30px 25px 30px;\r\n}\r\n\r\n\r\n\r\n/*******************\r\n Topbar\r\n*******************/\r\n\r\n.topbar {\r\n  position: relative;\r\n  z-index: 50;\r\n  .top-navbar {\r\n    min-height: 50px;\r\n    padding: 0px 15px 0 0;\r\n    .dropdown-toggle::after {\r\n      display: none;\r\n    }\r\n    .navbar-header {\r\n      line-height: 45px;\r\n      text-align: center;\r\n      .navbar-brand {\r\n        margin-right: 0px;\r\n        padding-bottom: 0px;\r\n        padding-top: 0px;\r\n        .light-logo {\r\n          display: none;\r\n        }\r\n        b {\r\n          line-height: 60px;\r\n          display: inline-block;\r\n        }\r\n      }\r\n    }\r\n    .navbar-nav>.nav-item>.nav-link {\r\n      padding-left: .75rem;\r\n      padding-right: .75rem;\r\n      font-size: 22px;\r\n      line-height: 40px;\r\n    }\r\n    .navbar-nav>.nav-item.show {\r\n      background: $dark-transparent;\r\n    }\r\n  }\r\n  .profile-pic {\r\n    width: 30px;\r\n    border-radius: 100%;\r\n  }\r\n  .dropdown-menu {\r\n    box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);\r\n    -webkit-box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);\r\n    -moz-box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);\r\n    border-color: $border;\r\n    .dropdown-item {\r\n      padding: 7px 1.5rem;\r\n    }\r\n  }\r\n  ul.dropdown-user {\r\n    padding: 0px;\r\n    min-width: 270px;\r\n    li {\r\n      list-style: none;\r\n      padding: 0px;\r\n      margin: 0px;\r\n      &.divider {\r\n        height: 1px;\r\n        margin: 9px 0;\r\n        overflow: hidden;\r\n        background-color: $border;\r\n      }\r\n      .dw-user-box {\r\n        padding: 10px 15px;\r\n        .u-img {\r\n          width: 45px;;\r\n          display: inline-block;\r\n          vertical-align: top;\r\n          img {\r\n            width: 100%;\r\n            border-radius: 5px;\r\n          }\r\n        }\r\n        .u-text {\r\n          display: inline-block;\r\n          padding-left: 10px;\r\n          h4 {\r\n            margin: 0px;\r\n            font-size: 15px;\r\n          }\r\n          p {\r\n            margin-bottom: 2px;\r\n            font-size: 12px;\r\n          }\r\n          .btn {\r\n            color: $white;\r\n            padding: 5px 10px;\r\n            display: inline-block;\r\n            &:hover {\r\n              background: $danger-dark;\r\n            }\r\n          }\r\n        }\r\n      }\r\n      a {\r\n        padding: 9px 15px;\r\n        display: block;\r\n        color: $bodytext;\r\n        &:hover {\r\n          background: $light;\r\n          color: $themecolor;\r\n          text-decoration: none;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.search-box {\r\n  .app-search {\r\n    position: absolute;\r\n    margin: 0px;\r\n    display: block;\r\n    z-index: 110;\r\n    width: 100%;\r\n    top: -1px;\r\n    box-shadow: 2px 0px 10px rgba(0, 0, 0, 0.2);\r\n    display: none;\r\n    left: 0px;\r\n    input {\r\n      width: 100.5%;\r\n      padding: 20px 40px 20px 20px;\r\n      border-radius: 0px;\r\n      font-size: 17px;\r\n      transition: 0.5s ease-in;\r\n      &:focus {\r\n        border-color: $white;\r\n      }\r\n    }\r\n    .srh-btn {\r\n      position: absolute;\r\n      top: 23px;\r\n      cursor: pointer;\r\n      background: $white;\r\n      width: 15px;\r\n      height: 15px;\r\n      right: 20px;\r\n      font-size: 14px;\r\n    }\r\n  }\r\n}\r\n\r\n/*******************\r\n Breadcrumb and page title\r\n*******************/\r\n\r\n.page-titles {\r\n  // background: $extra-light;\r\n  margin: 0 0px 30px;\r\n  padding: 15px 60px;\r\n  position: relative;\r\n  z-index: 10;\r\n  // box-shadow: 1px 0 5px rgba(0, 0, 0, 0.1);\r\n  h3 {\r\n    margin-bottom: 0px;\r\n    margin-top: 0px;\r\n  }\r\n  .breadcrumb {\r\n    padding: 0px;\r\n    background: transparent;\r\n    font-size: 14px;\r\n    li {\r\n      margin-top: 0px;\r\n      margin-bottom: 0px;\r\n    }\r\n    .breadcrumb-item+.breadcrumb-item::before {\r\n      content: \"\\e649\";\r\n      font-family: themify;\r\n      color: $light-text;\r\n      font-size: 11px;\r\n    }\r\n    .breadcrumb-item.active {\r\n      color: $muted;\r\n    }\r\n  }\r\n}\r\n\r\n\r\n\r\n/*******************\r\n Right side toggle\r\n*******************/\r\n\r\n\r\n.right-side-toggle {\r\n  position: relative;\r\n     \r\n}\r\n.btn-circle.right-side-toggle{\r\n  position:fixed;\r\n  bottom:20px;\r\n  right:20px;\r\n  padding:25px;\r\n  z-index:10;\r\n}\r\n.right-side-toggle i {\r\n  -webkit-transition-property: -webkit-transform;\r\n  -webkit-transition-duration: 1s;\r\n  -moz-transition-property: -moz-transform;\r\n  -moz-transition-duration: 1s;\r\n  transition-property: transform;\r\n  transition-duration: 1s;\r\n  \r\n  position: absolute;\r\n  top: 16px;\r\n  left: 16px;\r\n}\r\n\r\n.right-sidebar {\r\n  position: fixed;\r\n  right: -240px;\r\n  width: 240px;\r\n  display: none;\r\n  z-index: 1100;\r\n  background: $white;\r\n  top: 0px;\r\n  padding-bottom: 20px;\r\n  height: 100%;\r\n  box-shadow: 5px 1px 40px rgba(0, 0, 0, 0.1);\r\n  transition: all 0.3s ease;\r\n  .rpanel-title {\r\n    display: block;\r\n    padding: 19px 20px;\r\n    color: $white;\r\n    text-transform: uppercase;\r\n    font-size: 15px;\r\n    background: $themecolor;\r\n    span {\r\n      float: right;\r\n      cursor: pointer;\r\n      font-size: 11px;\r\n      &:hover {\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n  .r-panel-body {\r\n    height:100%;\r\n    position: absolute;\r\n    width: 100%;\r\n    padding-bottom: 40px;  \r\n    ul {\r\n      margin: 0px;\r\n      padding: 0px 20px;\r\n      li {\r\n        list-style: none;\r\n        padding: 5px 0;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.shw-rside {\r\n  right: 0px;\r\n  width: 240px;\r\n  display: block;\r\n}\r\n\r\n/*******************\r\n page title\r\n*******************/\r\n\r\n.page-titles {\r\n  padding-bottom: 20px;\r\n}\r\n\r\n\r\n\r\n/*******************\r\n Footer\r\n*******************/\r\n\r\n.footer {\r\n  bottom: 0;\r\n  color: $bodytext;\r\n  left: 0px;\r\n  padding: 17px 15px;\r\n  position: absolute;\r\n  right: 0;\r\n  border-top: 1px solid $border;\r\n  background: $white;\r\n}\r\n\r\n\r\n\r\n/*******************\r\n Card title\r\n*******************/\r\n\r\n.card {\r\n  margin-bottom: 30px;\r\n  .card-subtitle {\r\n    font-weight: 300;\r\n    margin-bottom: 15px;\r\n    color: $muted;\r\n  }\r\n}\r\n\r\n.card-inverse .card-bodyquote .blockquote-footer,\r\n.card-inverse .card-link,\r\n.card-inverse .card-subtitle,\r\n.card-inverse .card-text {\r\n  color: rgba(255, 255, 255, .65);\r\n}\r\nhtml body .card{\r\n&.card-success {\r\n  background: $success;\r\n  border-color: $success;\r\n}\r\n\r\n&.card-danger {\r\n  background: $danger;\r\n  border-color: $danger;\r\n}\r\n\r\n&.card-warning {\r\n  background: $warning;\r\n  border-color: $warning;\r\n}\r\n\r\n&.card-info {\r\n  background: $info;\r\n  border-color: $info;\r\n}\r\n\r\n&.card-primary {\r\n  background: $primary;\r\n  border-color: $primary;\r\n}\r\n\r\n&.card-dark {\r\n  background: $inverse;\r\n  border-color: $inverse;\r\n}\r\n\r\n&.card-megna {\r\n  background: $megna;\r\n  border-color: $megna;\r\n}\r\n}\r\n/*============================================================== \r\n Cards page\r\n ============================================================== */\r\n\r\n.card-actions {\r\n  float: $rgt;\r\n  a {\r\n    cursor: pointer;\r\n    color: $bodytext;\r\n    opacity: 0.7;\r\n    padding-left: 7px;\r\n    font-size: 13px;\r\n    &:hover {\r\n      opacity: 1;\r\n    }\r\n  }\r\n}\r\n\r\n.card-columns .card {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.collapsing {\r\n  -webkit-transition: height .08s ease;\r\n  transition: height .08s ease;\r\n}\r\n\r\n.card-info {\r\n  background: $info;\r\n  border-color: $info;\r\n}\r\n\r\n.card-primary {\r\n  background: $primary;\r\n  border-color: $primary;\r\n}\r\n\r\n.card-outline-info {\r\n  border-color: $info;\r\n  .card-header {\r\n    background: $info;\r\n    border-color: $info;\r\n  }\r\n}\r\n\r\n.card-outline-inverse {\r\n  border-color: $inverse;\r\n  .card-header {\r\n    background: $inverse;\r\n    border-color: $inverse;\r\n  }\r\n}\r\n\r\n.card-outline-warning {\r\n  border-color: $warning;\r\n  .card-header {\r\n    background: $warning;\r\n    border-color: $warning;\r\n  }\r\n}\r\n\r\n.card-outline-success {\r\n  border-color: $success;\r\n  .card-header {\r\n    background: $success;\r\n    border-color: $success;\r\n  }\r\n}\r\n\r\n.card-outline-danger {\r\n  border-color: $danger;\r\n  .card-header {\r\n    background: $danger;\r\n    border-color: $danger;\r\n  }\r\n}\r\n\r\n.card-outline-primary {\r\n  border-color: $primary;\r\n  .card-header {\r\n    background: $primary;\r\n    border-color: $primary;\r\n  }\r\n}\r\n\r\n/*============================================================== \r\n Buttons page\r\n ============================================================== */\r\n\r\n.button-group {\r\n  .btn {\r\n    margin-bottom: 5px;\r\n    margin-right: 5px;\r\n  }\r\n}\r\n\r\n.no-button-group {\r\n  .btn {\r\n    margin-bottom: 5px;\r\n    margin-right: 0px;\r\n  }\r\n}\r\n\r\n.btn {\r\n  .text-active {\r\n    display: none;\r\n  }\r\n  &.active .text-active {\r\n    display: inline-block;\r\n  }\r\n  &.active .text {\r\n    display: none;\r\n  }\r\n}\r\n\r\n\r\n/*============================================================== \r\nBreadcrumb\r\n ============================================================== */\r\n\r\n.bc-colored {\r\n  .breadcrumb-item,\r\n  .breadcrumb-item a {\r\n    color: $white;\r\n    &.active {\r\n      opacity: 0.7;\r\n    }\r\n  }\r\n  .breadcrumb-item+.breadcrumb-item::before {\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n\r\n.breadcrumb {\r\n  margin-bottom: 0px;\r\n}\r\n\r\n\r\n\r\n/*============================================================== \r\n Ui-bootstrap\r\n ============================================================== */\r\n\r\nul.list-icons {\r\n  margin: 0px;\r\n  padding: 0px;\r\n  li {\r\n    list-style: none;\r\n    line-height: 30px;\r\n    margin: 5px 0;\r\n    transition: 0.2s ease-in;\r\n    a {\r\n      color: $bodytext;\r\n      &:hover {\r\n        color: $themecolor;\r\n      }\r\n    }\r\n    i {\r\n      font-size: 13px;\r\n      padding-right: 8px;\r\n    }\r\n  }\r\n}\r\n\r\nul.list-inline {\r\n  li {\r\n    display: inline-block;\r\n    padding: 0 8px;\r\n  }\r\n}\r\n\r\nul.two-part {\r\n  margin: 0px;\r\n  li {\r\n    width: 48.8%;\r\n  }\r\n}\r\n\r\n\r\n\r\n/*Accordion*/\r\n\r\nhtml body .accordion {\r\n  .card {\r\n    margin-bottom: 0px;\r\n  }\r\n}\r\n\r\n\r\n\r\n/*============================================================== \r\n sparkline chart\r\n ============================================================== */\r\nhtml body {\r\n  .jqstooltip,\r\n  .flotTip {\r\n    width: auto!important;\r\n    height: auto!important;\r\n    background: $dark;\r\n    color: $white;\r\n    padding: 5px 10px;\r\n  }\r\n}\r\n\r\nbody .jqstooltip {\r\n  border-color: transparent;\r\n  border-radius: 60px;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n/*============================================================== \r\nDashboard1 chart\r\n ============================================================== */\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nPagination\r\n******************/\r\n\r\n.pagination-circle {\r\n  li.active a {\r\n    background: $success;\r\n  }\r\n  li a {\r\n    width: 40px;\r\n    height: 40px;\r\n    background: $light;\r\n    border: 0px;\r\n    text-align: center;\r\n    border-radius: 100%;\r\n    &:first-child,\r\n    &:last-child {\r\n      border-radius: 100%;\r\n    }\r\n    &:hover {\r\n      background: $success;\r\n      color: $white;\r\n    }\r\n  }\r\n  li.disabled a {\r\n    background: $light;\r\n    color: $border;\r\n  }\r\n}\r\n\r\n\r\n\r\n\r\n/*******************\r\nTable-Layout\r\n******************/\r\n\r\n.table thead th,\r\n.table th {\r\n  border: 0px;\r\n}\r\n\r\n.color-table.primary-table thead th {\r\n  background-color: $primary;\r\n  color: $white;\r\n}\r\n\r\n.table-striped tbody tr:nth-of-type(odd) {\r\n  background: $light;\r\n}\r\n\r\n.color-table.success-table thead th {\r\n  background-color: $success;\r\n  color: $white;\r\n}\r\n\r\n.color-table.info-table thead th {\r\n  background-color: $info;\r\n  color: $white;\r\n}\r\n\r\n.color-table.warning-table thead th {\r\n  background-color: $warning;\r\n  color: $white;\r\n}\r\n\r\n.color-table.danger-table thead th {\r\n  background-color: $danger;\r\n  color: $white;\r\n}\r\n\r\n.color-table.inverse-table thead th {\r\n  background-color: $inverse;\r\n  color: $white;\r\n}\r\n\r\n.color-table.dark-table thead th {\r\n  background-color: $dark;\r\n  color: $white;\r\n}\r\n\r\n.color-table.red-table thead th {\r\n  background-color: $red;\r\n  color: $white;\r\n}\r\n\r\n.color-table.purple-table thead th {\r\n  background-color: $purple;\r\n  color: $white;\r\n}\r\n\r\n.color-table.muted-table thead th {\r\n  background-color: $muted;\r\n  color: $white;\r\n}\r\n\r\n.color-bordered-table.primary-bordered-table {\r\n  border: 2px solid $primary;\r\n  thead th {\r\n    background-color: $primary;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.success-bordered-table {\r\n  border: 2px solid $success;\r\n  thead th {\r\n    background-color: $success;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.info-bordered-table {\r\n  border: 2px solid $info;\r\n  thead th {\r\n    background-color: $info;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.warning-bordered-table {\r\n  border: 2px solid $warning;\r\n  thead th {\r\n    background-color: $warning;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.danger-bordered-table {\r\n  border: 2px solid $danger;\r\n  thead th {\r\n    background-color: $danger;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.inverse-bordered-table {\r\n  border: 2px solid $inverse;\r\n  thead th {\r\n    background-color: $inverse;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.dark-bordered-table {\r\n  border: 2px solid $dark;\r\n  thead th {\r\n    background-color: $dark;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.red-bordered-table {\r\n  border: 2px solid $red;\r\n  thead th {\r\n    background-color: $red;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.purple-bordered-table {\r\n  border: 2px solid $purple;\r\n  thead th {\r\n    background-color: $purple;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.color-bordered-table.muted-bordered-table {\r\n  border: 2px solid $muted;\r\n  thead th {\r\n    background-color: $muted;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-primary-table {\r\n  background-color: $light-primary;\r\n  thead th {\r\n    background-color: $primary;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $primary;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-success-table {\r\n  background-color: $light-success;\r\n  thead th {\r\n    background-color: $success;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $success;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-info-table {\r\n  background-color: $light-info;\r\n  thead th {\r\n    background-color: $info;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $info;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-warning-table {\r\n  background-color: $light-warning;\r\n  thead th {\r\n    background-color: $warning;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $warning;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-danger-table {\r\n  background-color: $light-danger;\r\n  thead th {\r\n    background-color: $danger;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $danger;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-inverse-table {\r\n  background-color: $light-inverse;\r\n  thead th {\r\n    background-color: $inverse;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $inverse;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-dark-table {\r\n  background-color: rgba(43, 43, 43, .8);\r\n  thead th {\r\n    background-color: $dark;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tr:hover {\r\n    background-color: $dark;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-red-table {\r\n  background-color: $light-danger;\r\n  thead th {\r\n    background-color: $red;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $red;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-purple-table {\r\n  background-color: $light-primary;\r\n  thead th {\r\n    background-color: $purple;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $purple;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.full-color-table.full-muted-table {\r\n  background-color: rgba(152, 166, 173, .2);\r\n  thead th {\r\n    background-color: $muted;\r\n    border: 0;\r\n    color: $white;\r\n  }\r\n  tbody td {\r\n    border: 0;\r\n  }\r\n  tr:hover {\r\n    background-color: $muted;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nTable- editable table\r\n******************/\r\n\r\n.dt-bootstrap {\r\n  display: block;\r\n}\r\n\r\n.paging_simple_numbers {\r\n  .pagination {\r\n    .paginate_button {\r\n      padding: 0px;\r\n      background: $white;\r\n      &:hover {\r\n        background: $white;\r\n      }\r\n      a {\r\n        padding: 2px 10px;\r\n        border: 0px;\r\n      }\r\n      &.active a,\r\n      &:hover a {\r\n        background: $info;\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n/*******************\r\nIcon list fontawesom\r\n******************/\r\n\r\n.icon-list-demo {\r\n  div {\r\n    cursor: pointer;\r\n    line-height: 60px;\r\n    white-space: nowrap;\r\n    color: $bodytext;\r\n    &:hover {\r\n      color: $dark;\r\n    }\r\n    p {\r\n      margin: 10px 0;\r\n      padding: 5px 0;\r\n    }\r\n  }\r\n  i {\r\n    -webkit-transition: all 0.2s;\r\n    -webkit-transition: font-size .2s;\r\n    display: inline-block;\r\n    font-size: 18px;\r\n    margin: 0 15px 0 10px;\r\n    text-align: left;\r\n    transition: all 0.2s;\r\n    transition: font-size .2s;\r\n    vertical-align: middle;\r\n    transition: all 0.3s ease 0s;\r\n  }\r\n  .col-md-4,\r\n  .col-3 {\r\n    border-radius: $radius;\r\n    &:hover {\r\n      background-color: $extra-light;\r\n    }\r\n  }\r\n}\r\n\r\n.icon-list-demo .div:hover i {\r\n  font-size: 2em;\r\n}\r\n\r\n\r\n\r\n/*******************\r\nIcon list material icon\r\n******************/\r\n\r\n.material-icon-list-demo .mdi {\r\n  font-size: 21px;\r\n}\r\n\r\n\r\n\r\n\r\n/*******************\r\nlist and media\r\n******************/\r\n\r\n.list-group a.list-group-item:hover {\r\n  background: $light;\r\n}\r\n\r\n.list-group-item.active,\r\n.list-group .list-group-item.active:hover {\r\n  background: $themecolor;\r\n  border-color: $themecolor;\r\n}\r\n\r\n.list-group-item.disabled {\r\n  color: $muted;\r\n  background: $light;\r\n}\r\n\r\n.media {\r\n  border: 1px solid $border;\r\n  margin-bottom: 10px;\r\n  padding: 15px;\r\n}\r\n\r\n\r\n\r\n/*******************\r\nTimeline page\r\n******************/\r\n\r\n.timeline {\r\n  position: relative;\r\n  padding: 20px 0 20px;\r\n  list-style: none;\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.timeline:before {\r\n  content: \" \";\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 50%;\r\n  width: 3px;\r\n  margin-left: -1.5px;\r\n  background-color: $light;\r\n}\r\n\r\n.timeline>li {\r\n  position: relative;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.timeline>li:before,\r\n.timeline>li:after {\r\n  content: \" \";\r\n  display: table;\r\n}\r\n\r\n.timeline>li:after {\r\n  clear: both;\r\n}\r\n\r\n.timeline>li:before,\r\n.timeline>li:after {\r\n  content: \" \";\r\n  display: table;\r\n}\r\n\r\n.timeline>li:after {\r\n  clear: both;\r\n}\r\n\r\n.timeline>li>.timeline-panel {\r\n  float: left;\r\n  position: relative;\r\n  width: 46%;\r\n  padding: 20px;\r\n  border: 1px solid $border;\r\n  border-radius: $radius;\r\n  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);\r\n  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);\r\n}\r\n\r\n.timeline>li>.timeline-panel:before {\r\n  content: \" \";\r\n  display: inline-block;\r\n  position: absolute;\r\n  top: 26px;\r\n  right: -8px;\r\n  border-top: 8px solid transparent;\r\n  border-right: 0 solid $border;\r\n  border-bottom: 8px solid transparent;\r\n  border-left: 8px solid $border;\r\n}\r\n\r\n.timeline>li>.timeline-panel:after {\r\n  content: \" \";\r\n  display: inline-block;\r\n  position: absolute;\r\n  top: 27px;\r\n  right: -7px;\r\n  border-top: 7px solid transparent;\r\n  border-right: 0 solid $white;\r\n  border-bottom: 7px solid transparent;\r\n  border-left: 7px solid $white;\r\n}\r\n\r\n.timeline>li>.timeline-badge {\r\n  z-index: 10;\r\n  position: absolute;\r\n  top: 16px;\r\n  left: 50%;\r\n  width: 50px;\r\n  height: 50px;\r\n  margin-left: -25px;\r\n  border-radius: 50% 50% 50% 50%;\r\n  text-align: center;\r\n  font-size: 1.4em;\r\n  line-height: 50px;\r\n  color: #fff;\r\n  overflow: hidden;\r\n}\r\n\r\n.timeline>li.timeline-inverted>.timeline-panel {\r\n  float: right;\r\n}\r\n\r\n.timeline>li.timeline-inverted>.timeline-panel:before {\r\n  right: auto;\r\n  left: -8px;\r\n  border-right-width: 8px;\r\n  border-left-width: 0;\r\n}\r\n\r\n.timeline>li.timeline-inverted>.timeline-panel:after {\r\n  right: auto;\r\n  left: -7px;\r\n  border-right-width: 7px;\r\n  border-left-width: 0;\r\n}\r\n\r\n.timeline-badge.primary {\r\n  background-color: $primary;\r\n}\r\n\r\n.timeline-badge.success {\r\n  background-color: $success;\r\n}\r\n\r\n.timeline-badge.warning {\r\n  background-color: $warning;\r\n}\r\n\r\n.timeline-badge.danger {\r\n  background-color: $danger;\r\n}\r\n\r\n.timeline-badge.info {\r\n  background-color: $info;\r\n}\r\n\r\n.timeline-title {\r\n  margin-top: 0;\r\n  color: inherit;\r\n  font-weight: 400;\r\n}\r\n\r\n.timeline-body>p,\r\n.timeline-body>ul {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.timeline-body>p+p {\r\n  margin-top: 5px;\r\n}\r\n\r\n\r\n\r\n\r\n/*******************\r\nError Page\r\n******************/\r\n\r\n.error-box {\r\n  height: 100%;\r\n  position: fixed;\r\n  background: url(../../assets/images/background/error-bg.jpg) no-repeat center center #fff;\r\n  width: 100%;\r\n  .footer {\r\n    width: 100%;\r\n    left: 0px;\r\n    right: 0px;\r\n  }\r\n}\r\n\r\n.error-body {\r\n  padding-top: 5%;\r\n  h1 {\r\n    font-size: 210px;\r\n    font-weight: 900;\r\n    line-height: 210px;\r\n  }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nLogin register and recover password Page\r\n******************/\r\n\r\n.login-register {\r\n  background-size: cover;\r\n  background-repeat: no-repeat;\r\n  background-position: center center;\r\n  height: 100%;\r\n  width: 100%;\r\n  padding: 5% 0;\r\n  position: fixed;\r\n}\r\n\r\n.login-box {\r\n  width: 400px;\r\n  margin: 0 auto;\r\n  .footer {\r\n    width: 100%;\r\n    left: 0px;\r\n    right: 0px;\r\n  }\r\n  .social {\r\n    display: block;\r\n    margin-bottom: 30px;\r\n  }\r\n}\r\n\r\n#recoverform {\r\n  display: none;\r\n}\r\n\r\n.login-sidebar {\r\n  padding: 0px;\r\n  margin-top: 0px;\r\n  .login-box {\r\n    right: 0px;\r\n    position: absolute;\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n\r\n\r\n\r\n/*******************\r\nPricing Page\r\n******************/\r\n\r\n.pricing-box {\r\n  position: relative;\r\n  text-align: center;\r\n  margin-top: 30px;\r\n}\r\n\r\n.featured-plan {\r\n  margin-top: 0px;\r\n  .pricing-body {\r\n    padding: 60px 0;\r\n    background: $extra-light;\r\n    border: 1px solid #ddd;\r\n  }\r\n  .price-table-content .price-row {\r\n    border-top: 1px solid rgba(120, 130, 140, 0.13);\r\n  }\r\n}\r\n\r\n.pricing-body {\r\n  border-radius: 0px;\r\n  border-top: 1px solid rgba(120, 130, 140, 0.13);\r\n  border-bottom: 5px solid rgba(120, 130, 140, 0.13);\r\n  vertical-align: middle;\r\n  padding: 30px 0;\r\n  position: relative;\r\n}\r\n\r\n.pricing-body h2 {\r\n  position: relative;\r\n  font-size: 56px;\r\n  margin: 20px 0 10px;\r\n  font-weight: 500;\r\n  span {\r\n    position: absolute;\r\n    font-size: 15px;\r\n    top: -10px;\r\n    margin-left: -10px\r\n  }\r\n}\r\n\r\n.price-table-content {\r\n  .price-row {\r\n    padding: 20px 0;\r\n    border-top: 1px solid rgba(120, 130, 140, 0.13);\r\n  }\r\n}\r\n\r\n.pricing-plan {\r\n  padding: 0 15px;\r\n  .no-padding {\r\n    padding: 0px;\r\n  }\r\n}\r\n\r\n.price-lable {\r\n  position: absolute;\r\n  top: -10px;\r\n  padding: 5px 10px;\r\n  margin: 0 auto;\r\n  display: inline-block;\r\n  width: 100px;\r\n  left: 0px;\r\n  right: 0px;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n/*Documentation page*/\r\n\r\n.plugin-details {\r\n  display: none;\r\n}\r\n\r\n.plugin-details-active {\r\n  display: block;\r\n}\r\n\r\n.earning-box {\r\n  h6 {\r\n    font-weight: 500;\r\n    margin-bottom: 0px;\r\n    white-space: nowrap;\r\n  }\r\n  td,\r\n  th {\r\n    vertical-align: middle;\r\n  }\r\n}\r\n\r\n\r\n.btn-link{\r\n  border:0px;\r\n}\r\n/*******************\r\nSmart table\r\n******************/\r\n.smart-table tr td, .smart-table tr th{\r\n    padding:15px!important;\r\n}\r\n.ng2-smart-action-add-add{\r\n   color:$white!important;\r\n   background: $themecolor;\r\n   padding: 8px 15px;    \r\n   border-radius: $radius;    \r\n}\r\n.ng2-smart-pagination-nav{\r\n    margin-left: auto;\r\n}\r\n.ng2-smart-pagination-nav .pagination > li > a {\r\n    line-height: 1rem;\r\n} \r\n\r\n/*******************\r\nNGX Data table\r\n******************/\r\n\r\n.ngx-datatable.material {\r\n box-shadow:none!important;\r\n    border:1px solid $border;\r\n}\r\n\r\n/*******************\r\nChartistt chart css\r\n******************/\r\n.barchrt{\r\n    .ct-series-a .ct-bar{\r\n        stroke:$success;\r\n    }\r\n    .ct-series-b .ct-bar{\r\n        stroke:$info;\r\n    }\r\n}\r\n\r\n.linearea {\r\n  height: 280px;\r\n  .ct-series-a .ct-area {\r\n    fill-opacity: 0.05;\r\n    fill: $success;\r\n  }\r\n  .ct-series-a .ct-line,\r\n  .ct-series-a .ct-point {\r\n    stroke: $success;\r\n    stroke-width: 2px;\r\n  }\r\n  .ct-series-b .ct-area {\r\n    fill: $info;\r\n    fill-opacity: 0.1;\r\n  }\r\n  .ct-series-b .ct-line,\r\n  .ct-series-b .ct-point {\r\n    stroke: $info;\r\n    stroke-width: 2px;\r\n  }\r\n  .ct-series-c .ct-area {\r\n    fill: $danger;\r\n    fill-opacity: 0.1;\r\n  }\r\n  .ct-series-c .ct-line,\r\n  .ct-series-c .ct-point {\r\n    stroke: $danger;\r\n    stroke-width: 2px;\r\n  }\r\n  .ct-series-a .ct-point,\r\n  .ct-series-b .ct-point,\r\n  .ct-series-c .ct-point{\r\n    stroke-width: 6px;\r\n  }\r\n}\r\n.piechart{\r\n    .ct-series-a .ct-slice-donut{\r\n        stroke:$info\r\n    }\r\n    .ct-series-b .ct-slice-donut{\r\n        stroke:$success\r\n    }\r\n    .ct-series-c .ct-slice-donut{\r\n        stroke:$inverse\r\n    }\r\n    .ct-series-d .ct-slice-donut{\r\n        stroke:$danger\r\n    }\r\n    .ct-series-e .ct-slice-donut{\r\n        stroke:$warning\r\n    }\r\n}\r\n\r\n/*******************\r\nTaskboard scss\r\n******************/\r\n.taskboard {\r\n  overflow-x: auto;\r\n  overflow-y: auto;\r\n  white-space: nowrap;\r\n  padding: 8px;\r\n  .taskboard-wrapper {\r\n    width: 280px;\r\n    padding-right: 8px;\r\n    padding-left: 8px;\r\n    box-sizing: border-box;\r\n    display: inline-block;\r\n    vertical-align: top;\r\n    height: 100%;\r\n    &:first-child {\r\n      padding-left: 0;\r\n    }\r\n    &:last-child {\r\n      padding-right: 0;\r\n    }\r\n  }\r\n  .taskboard-list {\r\n    box-sizing: border-box;\r\n    display: flex;\r\n    flex-direction: column;\r\n    max-height: 100%;\r\n    white-space: normal;\r\n    background-color: rgba(0, 0, 0, 0.05);\r\n    border-radius: 0;\r\n  }\r\n  .taskboard-header {\r\n    position: relative;\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    text-transform: uppercase;\r\n    letter-spacing: 0.02rem;\r\n    padding: 8px 8px 0;\r\n  }\r\n  .taskboard-task {\r\n    position: relative;\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: 8px;\r\n    margin-bottom: 8px;\r\n    background: $white;\r\n    &:last-child {\r\n      margin-bottom: 0;\r\n    }\r\n    &:hover {\r\n      cursor: grab;\r\n    }\r\n  }\r\n  .taskboard-task:after {\r\n    content: \"\";\r\n    position: absolute;\r\n    border: 4px solid transparent;\r\n    top: 0;\r\n    border-top-width: 12px;\r\n    border-bottom-color: transparent;\r\n    right: 6px;\r\n  }\r\n  .taskboard-task.task-status-success:after {\r\n    border-top-color: $success;\r\n    border-right-color: $success;\r\n    border-left-color: $success;\r\n  }\r\n  .taskboard-task.task-status-info:after {\r\n    border-top-color: $info;\r\n    border-right-color: $info;\r\n    border-left-color: $info;\r\n  }\r\n  .taskboard-task.task-status-warning:after {\r\n    border-top-color: $warning;\r\n    border-right-color: $warning;\r\n    border-left-color: $warning;\r\n  }\r\n  .taskboard-task.task-status-danger:after {\r\n    border-top-color: $danger;\r\n    border-right-color: $danger;\r\n    border-left-color: $danger;\r\n  }\r\n  .taskboard-cards {\r\n    padding: 8px;\r\n    box-sizing: border-box;\r\n    overflow-x: hidden;\r\n    overflow-y: auto;\r\n  }\r\n  .taskboard-task-title {\r\n    margin-bottom: 8px;\r\n  }\r\n}\r\n\r\n// File Upload Plugin\r\n\r\n.afu-select-btn {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.afu-valid-file {\r\n    .progress {\r\n        width: 500px;\r\n        height: 13px;\r\n    }\r\n}\r\n\r\n#attachPin {\r\n    .label:empty {\r\n        display: none;\r\n    }\r\n}\r\n\r\n/*******************\r\nNotifier pages scss\r\n******************/\r\n.notifier__container * {\r\n  box-sizing: border-box;\r\n}\r\n\r\n.notifier__container-list {\r\n  margin-top: 0;\r\n  margin-bottom: 0;\r\n  padding-left: 0;\r\n  list-style-type: none;\r\n}\r\n\r\n.notifier__notification {\r\n  display: block;\r\n  position: fixed;\r\n  visibility: hidden;\r\n  z-index: 10000;\r\n  will-change: transform;\r\n  backface-visibility: hidden;\r\n}\r\n\r\n$notifier-shadow-color: rgba(0, 0, 0, .2) !default;\r\n\r\n.notifier__notification--material {\r\n  border-radius: 3px;\r\n  box-shadow: 0 1px 3px $notifier-shadow-color;\r\n  cursor: default; // Default cursor, even when hovering over text\r\n\r\n  padding: {\r\n    top: 11px;\r\n    right: 26px;\r\n    bottom: 10px;\r\n    left: 26px;\r\n  }\r\n\r\n  .notifier__notification {\r\n\r\n    &-message {\r\n      display: inline-block;\r\n\r\n      margin: {\r\n        // Reset paragraph default styles\r\n        top: 0;\r\n        bottom: 0;\r\n      }\r\n\r\n      vertical-align: top;\r\n      line-height: 32px;\r\n      font-size: 15px;\r\n    }\r\n\r\n    &-button {\r\n      display: inline-block;\r\n      transition: opacity .2s ease;\r\n      opacity: .5;\r\n\r\n      margin: {\r\n        right: -10px;\r\n        left: 10px;\r\n        top: -4px;\r\n      }\r\n\r\n      ;\r\n      outline: none;\r\n      border: none;\r\n      background: none;\r\n      cursor: pointer; // Make it obvious that the \"button\" (or, more honestly, icon) is clickable (#UX)\r\n      padding: 6px;\r\n      width: 32px;\r\n      height: 32px;\r\n      vertical-align: top;\r\n\r\n      &:hover,\r\n      &:focus {\r\n        opacity: 1; // Make me \"feel\" the clickability with a transparency change (#UX)\r\n      }\r\n\r\n      &:active {\r\n        transform: scale(.82); // Make me \"feel\" the click by a push back (#UX)\r\n        opacity: 1;\r\n      }\r\n\r\n    }\r\n\r\n  }\r\n\r\n}\r\n\r\n.notifier__notification--default {\r\n  background-color: $primary;\r\n  color: $white;\r\n\r\n  .notifier__notification-button-icon {\r\n    // 16x16 fixed size\r\n    fill: $white;\r\n  }\r\n\r\n}\r\n\r\n.notifier__notification--info {\r\n  background-color: $info;\r\n  color: $white;\r\n\r\n  .notifier__notification-button-icon {\r\n    // 16x16 fixed size\r\n    fill: $white;\r\n  }\r\n\r\n}\r\n\r\n.notifier__notification--warning {\r\n  background-color: $warning;\r\n  color: $white;\r\n\r\n  .notifier__notification-button-icon {\r\n    // 16x16 fixed size\r\n    fill: $white;\r\n  }\r\n\r\n}\r\n\r\n.notifier__notification--success {\r\n  background-color: $success;\r\n  color: $white;\r\n\r\n  .notifier__notification-button-icon {\r\n    // 16x16 fixed size\r\n    fill: $white;\r\n  }\r\n\r\n}\r\n\r\n.notifier__notification--error {\r\n  background-color: $danger;\r\n  color: $white;\r\n\r\n  .notifier__notification-button-icon {\r\n    // 16x16 fixed size\r\n    fill: $white;\r\n  }\r\n\r\n}\r\n\r\n/*******************\r\nNgx wizard pages scss\r\n******************/\r\n\r\n.wizard-navbar {\r\n  padding: 0px;\r\n  margin: 50px 0 50px 0;\r\n  display: flex;\r\n  position: relative;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  &::before {\r\n    height: 2px;\r\n    background: $border-color;\r\n    content: \"\";\r\n    position: absolute;\r\n    width: 75%;\r\n    margin: 0 auto;\r\n    left: 0;\r\n    right: 0;\r\n    top: 50%;\r\n    z-index: 1;\r\n  }\r\n\r\n  li {\r\n    list-style: none;\r\n    text-align: center;\r\n\r\n    .round-tabs {\r\n      padding: 20px;\r\n      background: white;\r\n      position: relative;\r\n      z-index: 2;\r\n      border: 1px solid $border-color;\r\n      border-radius: 100%;\r\n\r\n    }\r\n\r\n    >a {\r\n      padding: 0.5rem 0;\r\n    }\r\n\r\n    >a.active .round-tabs {\r\n      background: $info;\r\n      border-color: $info;\r\n      color: $white;\r\n    }\r\n  }\r\n}\r\n\r\n#ngx .ng-invalid,\r\n.ng-touched.ng-invalid {\r\n  border-color: $danger;\r\n}\r\n\r\n#ngx .ng-valid,\r\n.ng-touched.ng-valid {\r\n  border-color: $success;\r\n}\r\n","/*\r\nTemplate Name: Admin Press Admin\r\nAuthor: Wrappixel\r\nEmail: niravjoshi87@gmail.com\r\nFile: scss\r\n*/\r\n\r\n\r\n/*******************\r\nMain sidebar\r\n******************/\r\n\r\n.left-sidebar {\r\n    position: absolute;\r\n    width: 240px;\r\n    height: 100vh;\r\n    top: 0px;\r\n    z-index: 20;\r\n    padding-top: 15px;\r\n    background: $sidebar;\r\n    box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);\r\n}\r\n\r\n.fix-sidebar .left-sidebar {\r\n    position: fixed;\r\n}\r\n\r\n\r\n/*******************\r\nuse profile section\r\n******************/\r\n\r\n.user-profile {\r\n  position: relative;\r\n  background-size: cover;\r\n  .setpos {\r\n    top: -47px;\r\n    right: -3px;\r\n  }\r\n  .profile-img {\r\n    width: 70px;\r\n    margin: 0 auto;\r\n    padding: 10px 0 5px 0;\r\n    border-radius: 100%;\r\n    img {\r\n      width: 100%;\r\n      padding: 5px;\r\n      border: 1px solid $border;\r\n      border-radius: 100%;\r\n    }\r\n  }\r\n  .profile-text {\r\n    padding: 5px 0px;\r\n    position: relative;\r\n    text-align: center;\r\n    >a {\r\n      color: $muted;\r\n      padding: 0 5px;\r\n      &:hover {\r\n        color: $themecolor;\r\n      }\r\n      &:after {\r\n        display: none;\r\n      }\r\n    }\r\n  }\r\n  .dropdown-menu {\r\n    left: 0px!important;\r\n    right: 0px;\r\n    top: 62px!important;\r\n    width: 180px;\r\n    margin: 0 auto;\r\n  }\r\n}\r\n\r\n\r\n/*******************\r\nsidebar navigation\r\n******************/\r\n\r\n.scroll-sidebar {\r\n    height: calc(100vh - 70px);\r\n    position: relative;\r\n    &.ps .ps__rail-y {\r\n        left: 2px;\r\n        right: auto;\r\n        background: none;\r\n        width: 6px;\r\n        /* If using `left`, there shouldn't be a `right`. */\r\n    }\r\n}\r\n\r\n.collapse.in {\r\n    display: block;\r\n}\r\n.nav-small-cap {\r\n        font-size: 12px;\r\n        margin-bottom: 0px;\r\n        padding: 14px 14px 14px 20px;\r\n        color: $dark;\r\n        font-weight: 500;\r\n    }\r\n.sidebar-nav {\r\n    \r\n    padding: 15px 0 0 0px;\r\n    ul {\r\n        margin: 0px;\r\n        padding: 0px;\r\n        li {\r\n            list-style: none;\r\n            a {\r\n                color: $sidebar-text;\r\n                padding: 8px 35px 8px 15px;\r\n                display: block;\r\n                font-size: 14px;\r\n                font-weight: 400;\r\n                &.active,\r\n                &:hover {\r\n                    color: $themecolor;\r\n                   \r\n                    i {\r\n                        color: $themecolor;\r\n                    }\r\n                }\r\n                &.active {\r\n                    font-weight: 500;\r\n                    color: $dark;\r\n                    \r\n                }\r\n            }\r\n            ul {\r\n                padding-left: 36px;\r\n                \r\n                ul {\r\n                    padding-left: 15px;\r\n                }\r\n            }\r\n            &.nav-devider {\r\n                height: 1px;\r\n                background: $border;\r\n                display: block;\r\n                margin: 15px 0;\r\n            }\r\n        }\r\n    }\r\n    > ul > li > a {\r\n        i {\r\n            width: 31px;\r\n            font-size: 24px;\r\n            display: inline-block;\r\n            vertical-align: middle;\r\n            color: $sidebar-icons;\r\n        }\r\n        .label {\r\n            float: right;\r\n            margin-top: 6px;\r\n        }\r\n        &.active {\r\n            font-weight: 400;\r\n            background: #242933;\r\n            color: $themecolor-alt;\r\n        }\r\n    }\r\n    > ul > li {\r\n        margin-bottom: 5px;\r\n        &.active > a {\r\n            color: $themecolor;\r\n            font-weight: 500;\r\n            i {\r\n                color: $themecolor;\r\n            }\r\n        }\r\n    }\r\n    .waves-effect {\r\n        transition: none;\r\n        -webkit-transition: none;\r\n        -o-transition: none;\r\n    }\r\n}\r\n\r\n.sidebar-nav .has-arrow {\r\n    position: relative;\r\n    &::after {\r\n        position: absolute;\r\n        content: '';\r\n        width: 7px;\r\n        height: 7px;\r\n        border-width: 1px 0 0 1px;\r\n        border-style: solid;\r\n        border-color: $sidebar-text;\r\n        right: 1em;\r\n        -webkit-transform: rotate(135deg) translate(0, -50%);\r\n        -ms-transform: rotate(135deg) translate(0, -50%);\r\n        -o-transform: rotate(135deg) translate(0, -50%);\r\n        transform: rotate(135deg) translate(0, -50%);\r\n        -webkit-transform-origin: top;\r\n        -ms-transform-origin: top;\r\n        -o-transform-origin: top;\r\n        transform-origin: top;\r\n        top: 23px;\r\n        -webkit-transition: all .3s ease-out;\r\n        -o-transition: all .3s ease-out;\r\n        transition: all .3s ease-out;\r\n    }\r\n}\r\n\r\n.sidebar-nav .active > .has-arrow::after,\r\n.sidebar-nav li > .has-arrow.active::after,\r\n.sidebar-nav .has-arrow[aria-expanded=\"true\"]::after {\r\n    -webkit-transform: rotate(-135deg) translate(0, -50%);\r\n    -ms-transform: rotate(-135deg) translate(0, -50%);\r\n    -o-transform: rotate(-135deg) translate(0, -50%);\r\n    top: 45%;\r\n    width: 7px;\r\n    transform: rotate(-135deg) translate(0, -50%);\r\n}\r\n\r\n\r\n\r\n","/*\r\nTemplate Name: Admin Press Admin\r\nAuthor: Wrappixel\r\nEmail: niravjoshi87@gmail.com\r\nFile: scss\r\n*/\r\n\r\n/*******************\r\nMain sidebar\r\n******************/\r\n.top-header{\r\n    margin-top: 10px;\r\n}\r\n.top-nav {\r\n    background: linear-gradient( 206deg, #9b33e4, #6532ce);\r\n\r\n                \r\n    ul {\r\n        margin: 0px;\r\n        padding: 0px;\r\n        display: flex;\r\n        max-width: 90%;\r\n        justify-content: space-around;\r\n        margin: 0px auto;\r\n        &.collapse.in {\r\n            position: absolute;\r\n            z-index: 99;\r\n            background-color: $white;\r\n            padding: 0px 20px;\r\n            width: auto;\r\n            a,\r\n            i {\r\n                color: $themecolor;\r\n            }\r\n        }\r\n        li {\r\n            list-style: none;\r\n            a {\r\n                color: $sidebar-text;\r\n                padding: 8px 15px 8px 15px;\r\n                display: block;\r\n                font-size: 14px;\r\n                font-weight: 400;\r\n                border-bottom: 1px solid transparent;\r\n                &.active,\r\n                &:hover {\r\n                    color: $themecolor;\r\n                    background-color: $white;\r\n                    border-bottom: 1px solid $themecolor;\r\n\r\n                    i {\r\n                        color: $sidebar-text;\r\n                    }\r\n                }\r\n                &.active {\r\n                    color: $themecolor;\r\n                    background-color: $white;\r\n                    border-bottom: 1px solid $themecolor;\r\n                }\r\n            }\r\n            ul {\r\n                padding-left: 36px;\r\n\r\n                ul {\r\n                    padding-left: 15px;\r\n                }\r\n            }\r\n            &.nav-devider {\r\n                height: 1px;\r\n                background: $border;\r\n                display: block;\r\n                margin: 15px 0;\r\n            }\r\n        }\r\n    }\r\n    > ul > li > a {\r\n        i {\r\n            width: 31px;\r\n            font-size: 24px;\r\n            display: inline-block;\r\n            vertical-align: middle;\r\n            color: $sidebar-icons;\r\n        }\r\n        .label {\r\n            float: right;\r\n            margin-top: 6px;\r\n        }\r\n        &.active {\r\n            font-weight: 400;\r\n            background: #242933;\r\n            color: $themecolor-alt;\r\n        }\r\n    }\r\n    > ul > li {\r\n        // margin-bottom: 5px;\r\n        &.active > a,\r\n        &:hover > a {\r\n            color: $themecolor;\r\n            // font-weight: 500;\r\n            background-color: $white;\r\n            border-bottom: 1px solid $themecolor;\r\n            i {\r\n                color: $themecolor;\r\n            }\r\n        }\r\n    }\r\n    .waves-effect {\r\n        transition: none;\r\n        -webkit-transition: none;\r\n        -o-transition: none;\r\n    }\r\n}\r\n\r\n.sidebar-nav .has-arrow {\r\n    position: relative;\r\n    &::after {\r\n        position: absolute;\r\n        content: \"\";\r\n        width: 7px;\r\n        height: 7px;\r\n        border-width: 1px 0 0 1px;\r\n        border-style: solid;\r\n        border-color: $sidebar-text;\r\n        right: 1em;\r\n        -webkit-transform: rotate(135deg) translate(0, -50%);\r\n        -ms-transform: rotate(135deg) translate(0, -50%);\r\n        -o-transform: rotate(135deg) translate(0, -50%);\r\n        transform: rotate(135deg) translate(0, -50%);\r\n        -webkit-transform-origin: top;\r\n        -ms-transform-origin: top;\r\n        -o-transform-origin: top;\r\n        transform-origin: top;\r\n        top: 23px;\r\n        -webkit-transition: all 0.3s ease-out;\r\n        -o-transition: all 0.3s ease-out;\r\n        transition: all 0.3s ease-out;\r\n    }\r\n}\r\n","/* Padding margin for rtl */\r\n\r\n$vertical-property-map: (m-r-: margin-left, m-l-: margin-right, p-r-: padding-left, p-l-: padding-right);\r\n$sizes-list: 5 10 15 20 25 30 40;\r\n@each $size in $sizes-list {\r\n    $val: $size + 0px;\r\n    @each $keyword,\r\n    $property in $vertical-property-map {\r\n        html body .rtl-theme .#{$keyword}#{$size} {\r\n            #{$property}: $val;\r\n        }\r\n    }\r\n}\r\n\r\n/* Discarding margin padding of ltr */\r\n\r\n$ltr-property-map: (m-r-: margin-right, m-l-: margin-left, p-r-: padding-right, p-l-: padding-left);\r\n$sizes-list: 5 10 15 20 25 30 40;\r\n@each $size in $sizes-list {\r\n    @each $keyword,\r\n    $property in $ltr-property-map {\r\n        html body .rtl-theme .#{$keyword}#{$size} {\r\n            #{$property}: 0;\r\n        }\r\n    }\r\n}\r\n\r\n/* Zero margin padding for rtl */\r\n\r\n$zero-property-map: (m-r: margin-left, m-l: margin-right, p-r: padding-left, p-l: padding-right);\r\n@each $keyword,\r\n$property in $zero-property-map {\r\n    html body .rtl-theme .#{$keyword}-0 {\r\n        #{$property}: 0;\r\n    }\r\n}\r\n","// this is margin-padding class override\r\n\r\n@import 'rtl-padding-margin';\r\n\r\n// this is bootstrap override\r\n\r\n.rtl-theme {\r\n\r\n    text-align: right;\r\n    direction: rtl;\r\n\r\n    .page-wrapper{\r\n        margin-left: 0px;\r\n        margin-right: 240px;\r\n    }\r\n\r\n    .topbar .top-navbar{\r\n        padding: 0px 0px 0 15px;\r\n    }\r\n\r\n    .footer{\r\n        left: 0px;\r\n        right: 240px;\r\n    }\r\n\r\n    .custom-control {\r\n        padding-left: 0;\r\n        padding-right: 1.5rem;\r\n    }\r\n\r\n    .custom-control-label::after,\r\n    .custom-control-label::before {\r\n        left: auto;\r\n        right: -25px;\r\n    }\r\n\r\n    .custom-file-label {\r\n        left: auto;\r\n        right: 0;\r\n        width: 100%;\r\n        &::after {\r\n            right: auto;\r\n            left: 0;\r\n        }\r\n    }\r\n\r\n    .shw-rside{\r\n        left: 0px;\r\n        right: unset;\r\n    }\r\n\r\n    .btn-circle.right-side-toggle{\r\n        left: 20px;\r\n        right: 0;\r\n    }\r\n\r\n    .page-titles .breadcrumb{\r\n        float: left;\r\n    }\r\n\r\n    .right-sidebar .rpanel-title span{\r\n        float: left;\r\n    }\r\n\r\n    .scroll-sidebar {\r\n        &.ps .ps__rail-y {\r\n            left: auto;\r\n            right: 2px;\r\n        }\r\n    }\r\n\r\n    .sidebar-nav {\r\n        > ul > li > a {\r\n            .label {\r\n                float: left;\r\n                margin-left: 12px;\r\n            }\r\n        }\r\n    }\r\n\r\n    // Topbar\r\n    .topbar {\r\n        .top-navbar .navbar-header{\r\n            padding-left: 0;\r\n            padding-right: 10px;\r\n        }\r\n        ul.dropdown-user {\r\n            li {\r\n                .dw-user-box {\r\n                    .u-text {\r\n                        padding-left: 0px;\r\n                        padding-right: 10px;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        .profile-pic{\r\n            border-right: 1px solid rgba(120, 130, 140, 0.13);\r\n            border-left: 0;\r\n        }\r\n    }\r\n\r\n    /* Mini-Sidebar in RTL */\r\n    &.mini-sidebar .page-wrapper{\r\n        margin-right: 70px;\r\n        margin-left: 0;\r\n    }\r\n\r\n    .custom-select {\r\n        padding: .375rem .75rem .375rem 1.75rem;\r\n        //background: url(../../../assets/images/custom-select.png) left 0.35rem center no-repeat $white;\r\n    }\r\n\r\n    .list-group {\r\n        padding: 0px;\r\n    }\r\n\r\n    .float-left {\r\n        float: right!important;\r\n    }\r\n    .float-right {\r\n        float: left!important;\r\n    }\r\n\r\n    .text-left {\r\n        text-align: right!important;\r\n    }\r\n    .text-right {\r\n        text-align: left!important;\r\n    }\r\n\r\n    .close {\r\n        float: left;\r\n    }\r\n\r\n    .modal-header .close {\r\n        float: left;\r\n      